<template>
  <div class="ranking">
    <div class="top">
      <van-tabs v-model="active" animated>
        <van-tab
          v-for="(item, index) in Ranking"
          :title="item.title"
          :key="index"
        >
          <div class="rankinglist">
            <ul>
              <li
                v-for="(sonItem, index) in item.order"
                :key="index"
                @click="sonItemIndex = index"
                :style="{ background: ListColorOne[active][index] }"
              >
                <img src="" alt="" />
                <p
                  :style="{ color: ListTypeColor[active][index] }"
                  :class="{ listdark: sonItemIndex == index }"
                >
                  {{ sonItem.title }}
                </p>
              </li>
            </ul>
          </div>
        </van-tab>
      </van-tabs>
      <div class="back" @click="back">三( ᐛ )</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sonItemIndex: 0,
      active: 0,
      ListColorOne: [
        ["#bfffd4", "#ded6ff", "#ffe6bc", "#ffcaca", "#ffe6bc", "#bde9ff"],
        ["#ffe6bc", "#bde9ff"],
        ["#bde9ff", "#ffcaca"],
      ],
      ListTypeColor: [
        ["#1ccca4", "#a38dff", "#ffad2c", "#ef4868", "#ffad2c", "#46b5fb"],
        ["#ffad2c", "#46b5fb"],
        ["#46b5fb", "#ef4868"],
      ],
    };
  },
  created() {
    this.$store.commit("falseFooterFlag");
    this.$store.commit("RankingID");
  },
  activated() {
    this.$store.commit("falseFooterFlag");
    this.$store.commit("RankingID");
  },
  computed: {
    Ranking() {
      return this.$store.state.Ranking;
    },
  },
  methods: {
    back() {
      this.$router.go(-1);
      this.$store.commit("trueFooterFlag");
    },
  },
};
</script>

<style lang='less'>
.ranking {
  width: 100%;
  .top {
    position: relative;
  }
  .van-tabs__wrap {
    padding: 0 100px;
    .van-tab--active {
      color: #fc6454;
    }
    .van-tabs__line {
      width: 30px;
      background-color: #fc6454;
    }
  }
  .van-tab__pane {
    .rankinglist {
      margin-top: 20px;
      ul {
        display: flex;
        overflow: scroll;
        margin: 0 10px;
        margin-bottom: 10px;
        &::-webkit-scrollbar {
          display: none;
        }
        li {
          padding: 10px 5px;
          margin: 0 10px;
          height: 80px;
          background: #fc6454;
          white-space: nowrap;
          display: flex;
          align-items: flex-end;
          border-radius: 5px;
          p {
            font-size: 12px;
            &.listdark{
              &::before {
                content: "◆";
              }
              &::after {
                content: "◆";
              }
            }
            &::before {
              content: "◇";
            }
            &::after {
              content: "◇";
            }
          }
        }
        .sonItemIndex {
          text-shadow: 0 2px 2px rgba(0, 0, 0, 0.7);
        }
      }
    }
  }
  .back {
    height: 44px;
    line-height: 44px;
    position: absolute;
    top: 0;
    left: 10px;
    color: #fc6454;
  }
}
</style>